"use client";

import { Key, useCallback, useEffect, useMemo, useState } from "react";
import {
  Table,
  TableHeader,
  TableColumn,
  TableBody,
  TableRow,
  TableCell,
  type SortDescriptor,
} from "@heroui/table";

import {
  Dropdown,
  DropdownItem,
  DropdownMenu,
  DropdownTrigger,
} from "@heroui/dropdown";
import { Button } from "@heroui/button";
import { Chip } from "@heroui/chip";
import { User } from "@heroui/user";
import { TopToolbar } from "./toolbar/top";
import { BottomToolbar } from "./toolbar/bottom";
import { PiDotsThreeOutlineVerticalFill } from "react-icons/pi";
import { getError, getRequest } from "@/utils/axios";
import { ENDPOINTS } from "@/shared/site";
import { AxiosErrorProps } from "@/types";
import { useAlertStore } from "@/providers/alert";
import useDate from "@/hooks/date";
import { SharedSelection } from "@heroui/system";

export const columns = [
  { name: "IDENTITY", key: "first_name", sortable: true },
  { name: "ROLE", key: "role", sortable: true },
  { name: "ACTIVE", key: "is_active", sortable: true },
  { name: "BLOCKED", key: "is_blocked", sortable: true },
  { name: "CREATED AT", key: "created_at", sortable: true },
];

const INITIAL_VISIBLE_COLUMNS = [
  "first_name",
  "role",
  "is_active",
  "is_blocked",
  "created_at",
];

interface ListResultProps {
  id: number;
  first_name: string;
  last_name: string;
  email: string;
  picture: {
    portrait: null | string;
  };
  role: "user" | "admin" | "superadmin";
  created_at: string;
  last_password_update: null | string;
  regular_auth: boolean;
  is_active: boolean;
  is_blocked: boolean;
}

interface TableListProps {
  page: number;
  total: number;
  page_size: number;
  order: SortDescriptor;
  results: ListResultProps[];
}

export function CustomTable() {
  const [list, setList] = useState<TableListProps>({
    page: 1,
    total: 0,
    page_size: 5,
    order: {
      column: "created_at",
      direction: "descending",
    },
    results: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const { setAlert } = useAlertStore();
  const { maskDate } = useDate();

  const [filterValue, setFilterValue] = useState("");
  const [selectedKeys, setSelectedKeys] = useState<SharedSelection>(
    new Set([])
  );
  const [visibleColumns, setVisibleColumns] = useState<SharedSelection>(
    new Set(INITIAL_VISIBLE_COLUMNS)
  );

  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [sortDescriptor, setSortDescriptor] = useState<SortDescriptor>({
    column: "created_at",
    direction: "descending",
  });
  const [page, setPage] = useState(1);

  const hasSearchFilter = Boolean(filterValue);

  const headerColumns = useMemo(() => {
    if (visibleColumns === "all") return columns;

    return columns.filter((column) =>
      Array.from(visibleColumns).includes(column.key)
    );
  }, [visibleColumns]);

  const handleSearchChange = useCallback((value) => {
    if (value) {
      setFilterValue(value);
      setPage(1);
    } else {
      setFilterValue("");
    }
  }, []);

  const getList = useCallback(
    async (
      page: number,
      page_size: number,
      column: Key,
      direction: "ascending" | "descending"
    ) => {
      try {
        const { data } = await getRequest({
          url: `${
            ENDPOINTS.user_list
          }?page=${page}&page_size=${rowsPerPage}&ordering=${
            direction === "ascending" ? column : `-${column}`
          }`,
        });
        setList((prev) => ({
          ...prev,
          pages: Math.ceil(data.count / rowsPerPage),
          page_size,
          total: data.count,
          order: { column, direction },
          results: data.results,
        }));
      } catch (err) {
        const { data, status } = getError(err as AxiosErrorProps);
        if (status && status >= 500) {
          setAlert((prev) => ({
            ...prev,
            isVisible: true,
            color: "danger",
            description: "Internal server error.",
          }));
        } else {
          setAlert((prev) => ({
            ...prev,
            isVisible: true,
            color: "danger",
            description: data?.message,
          }));
        }
      } finally {
        setIsLoading(true);
      }
    },
    []
  );

  const handleSortChange = useCallback(
    (descriptor: SortDescriptor) => {
      getList(
        list.page,
        list.page_size,
        descriptor.column,
        descriptor.direction
      );
    },
    [list]
  );

  const renderCell = useCallback(
    (item: ListResultProps, columnKey: keyof typeof item) => {
      const value = item[columnKey];
      if (columnKey === "first_name")
        return (
          <User
            avatarProps={{
              radius: "full",
              size: "sm",
              src: item.picture.portrait ?? "",
            }}
            classNames={{
              description: "text-default-500",
            }}
            description={item.email}
            name={`${item.first_name} ${item.last_name}`}
          >
            {item.email}
          </User>
        );
      if (columnKey === "is_blocked" || columnKey === "is_active")
        return (
          <Chip
            className="uppercase border-none gap-1 text-default-600"
            color={"success"}
            size="sm"
            variant="dot"
          >
            Active
          </Chip>
        );
      if (columnKey === "created_at")
        return maskDate(item.created_at, "dd/mm/yyyy");
      if (columnKey === "actions")
        return (
          <Dropdown className="bg-background border-1 border-default-200">
            <DropdownTrigger>
              <Button isIconOnly radius="full" size="sm" variant="light">
                <PiDotsThreeOutlineVerticalFill className="text-default-500 w-4 h-4" />
              </Button>
            </DropdownTrigger>
            <DropdownMenu>
              <DropdownItem key="view">View</DropdownItem>
              <DropdownItem key="edit">Edit</DropdownItem>
              <DropdownItem key="delete">Delete</DropdownItem>
            </DropdownMenu>
          </Dropdown>
        );
      if (columnKey === "role")
        return <span className="uppercase">{value}</span>;
      return value;
    },
    []
  );

  useEffect(() => {
    getList(list.page, list.page_size, list.order.column, list.order.direction);
  }, []);

  return (
    <Table
      isCompact
      removeWrapper
      aria-label="Example table with custom cells, pagination and sorting"
      bottomContentPlacement="outside"
      checkboxesProps={{
        size: "sm",
        classNames: {
          wrapper: "after:bg-foreground after:text-background text-background",
        },
      }}
      classNames={{
        wrapper: ["max-h-[382px]", "max-w-3xl"],
        th: [
          "bg-transparent",
          "text-default-500",
          "border-b",
          "border-divider",
        ],
        td: [
          "first:group-data-[first=true]/tr:before:rounded-none",
          "last:group-data-[first=true]/tr:before:rounded-none",
          "group-data-[middle=true]/tr:before:rounded-none",
          "first:group-data-[last=true]/tr:before:rounded-none",
          "last:group-data-[last=true]/tr:before:rounded-none",
        ],
      }}
      selectedKeys={selectedKeys}
      selectionMode="multiple"
      sortDescriptor={list.order}
      // topContent={
      //   <TopToolbar
      //     visibleColumns={visibleColumns}
      //     setVisibleColumns={setVisibleColumns}
      //     setPage={setPage}
      //     setRowsPerPage={setRowsPerPage}
      //     columns={columns}
      //   />
      // }
      bottomContent={
        list.results.length > 0 ? (
          <BottomToolbar
            selectedKeys={selectedKeys}
            list={list}
            getList={getList}
          />
        ) : null
      }
      topContentPlacement="outside"
      onSelectionChange={setSelectedKeys}
      onSortChange={handleSortChange}
    >
      <TableHeader
        columns={[
          ...headerColumns,
          { name: "ACTIONS", key: "actions", sortable: false },
        ]}
      >
        {(column) => (
          <TableColumn
            key={column.key}
            align={column.key === "actions" ? "end" : "start"}
            allowsSorting={column.sortable}
          >
            {column.name}
          </TableColumn>
        )}
      </TableHeader>
      <TableBody
        emptyContent={"No users found"}
        items={list.results}
        isLoading={isLoading}
      >
        {(item) => {
          return (
            <TableRow key={item.id}>
              {(columnKey) => (
                <TableCell>{renderCell(item, columnKey)}</TableCell>
              )}
            </TableRow>
          );
        }}
      </TableBody>
    </Table>
  );
}
