import { Pagination } from "@heroui/pagination";
import { useCallback } from "react";

export function BottomToolbar({ list, selectedKeys, getList }) {
  const handlePageChange = useCallback(
    (page: number) => {
      getList(page, list.page_size, list.order.column, list.order.direction);
    },
    [list]
  );

  return (
    <div className="py-2 px-2 flex justify-between items-center mt-8">
      <Pagination
        size="sm"
        isCompact
        showControls
        showShadow
        classNames={{
          cursor: "bg-primary text-background",
          prev: "cursor-pointer",
          next: "cursor-pointer",
          item: "cursor-pointer",
        }}
        // isDisabled={hasSearchFilter}
        page={list.page ?? 1}
        total={list.pages}
        onChange={handlePageChange}
      />
      <span className="text-small text-default-400">
        {selectedKeys === "all"
          ? "All items selected"
          : `${selectedKeys.size} of ${list.results.length} selected`}
      </span>
    </div>
  );
}
